    width: 24px;
    text-align: center;
    font-size: 18px;
    vertical-align: middle;
}
.container .topbar-wrap .topbar-account {
    float: right;
    padding-right: 12px;
}
.container .topbar-wrap .topbar-timer {
    display: inline-block;
}
.container .topbar-wrap .topbar-timer span {
    display: inline-block;
