/* left sidebar style start */
.container aside {
    min-width: 50px;
    background: #333744;
}
.container aside::-webkit-scrollbar {
    display: none;
}
.container aside .menu-toggle {
    background: #4a5064;
    text-align: center;
    color: white;
    height: 26px;
    line-height: 30px;
}
.container aside .menu-toggle .iconfont:hover {
    cursor: pointer;
}
aside .el-menu-item,
aside .el-submenu__title {
    color: #fff;
